<template>
  <div class="detail-information">
    <Description title='Название'>
      {{coin.name ? coin.name : '—'}}
    </Description>
    <Description title='Обозначение'>
      {{coin.symbol ? coin.symbol : '—'}}
    </Description>
    <Description title='Тип'>
      {{coin.type ? coin.type : '—'}}
    </Description>
    <Description title='Описание'>
      {{coin.description ? coin.description : '—'}}
    </Description>
    <Description title='Хеш алгоритм'>
      {{coin.hash_algorithm ? coin.hash_algorithm : '—'}}
    </Description>
    <Description title='Сайт'>
      <a :href='coin.links.website[0]' v-if='coin.links && coin.links.website'>{{coin.links.website[0]}}</a>
      <template v-else>—</template>
    </Description>
    <Description title='Появился'>
      {{coin.started_at ? new Date(coin.started_at).toLocaleDateString() : '—'}}
    </Description>
    <Description title='Статус разработки'>
      {{coin.development_status ? coin.development_status : '—'}}
    </Description>
    <Description title='Open source'>
      {{coin.open_source ? 'да' : ( coin.open_source === false ? 'нет' : '—')}}
    </Description>
  </div>
</template>

<script>
import Description from './Description.vue'

export default {
  name: 'DetailInformation',
  props: {
    coin: {
      type: Object,
      required: true
    }
  },
  components: {
    Description
  }
};
</script>