<template>
  <div class="coins-list">
    <ListHeader>
      <Field>Название</Field>
      <Field>Тип</Field>
      <Field>Обозначение</Field>
      <Field>Новинка</Field>
      <Field>Подробнее</Field>
    </ListHeader>
    <Loading v-if='loading'/>
    <Alert v-if='alert'>Загрузка не удалась</Alert>
    <VirtualList :size="30" :remain="20" wtag='ul' wclass='coins-list__ul'>
      <ListItem :key='coin.id' v-for='coin in filteredCoins'>
        <Field>{{coin.name}}</Field>
        <Field>{{coin.type}}</Field>
        <Field>{{coin.symbol}}</Field>
        <Field>{{coin.is_new ? 'Новинка!' : ''}}</Field>
        <Field>
          <button class="coins-list__more-button" @click='openWindow(coin.id)'>Подробнее</button>
        </Field>
      </ListItem>
    </VirtualList>
    <Window 
      :title='"Подробная информация."' 
      v-if='popup.show' 
      @close='closeWindow'
    >
      <DetailInformation :coin='informationOfCoins[popup.coinId]' v-if='informationOfCoins[popup.coinId]' />
      <Loading v-if='popup.loading'/>
      <Alert v-if='popup.alert'>Загрузка не удалась</Alert>
    </Window>
  </div>
</template>

<script>
import VirtualList from 'vue-virtual-scroll-list'
import ListItem from './ListItem.vue'
import ListHeader from './ListHeader.vue'
import Field from './Field.vue'
import Window from './Window.vue'
import DetailInformation from './DetailInformation.vue'
import Loading from './Loading.vue'
import Alert from './Alert.vue'


export default {
  name: 'CoinsList', 
  components: {
    ListItem,
    Field,
    Window,
    VirtualList,
    ListHeader,
    DetailInformation,
    Loading,
    Alert
  },
  data() {
    return {
      coins: [],
      informationOfCoins: {},
      popup: {
        show: false,
        coinId: null,
        loading: false,
        alert: false
      },
      loading: false,
      alert: false
    }
  },
  created() {
    this.loading = true;
    this.loadCoins()
      .then(() => { this.loading = false })
  },
  computed: {
    filteredCoins() {
      return this.coins.filter(coin => coin.is_active)
    }
  },
  methods: {
    closeWindow() {
      this.popup.show = false;
      this.popup.alert = false;
    },
    async openWindow(id) {
      this.popup.show = true;
      this.popup.loading = true;
      this.popup.coinId = id;
      await this.loadInformationOfCoin(id);
      this.popup.loading = false;
    },
    async loadCoins(){
      try{
        let response = await fetch('https://api.coinpaprika.com/v1/coins', {method: 'GET'})
        if (response.status !== 200) {
          throw 'error loading'
        }
        this.coins = await response.json()
      }catch(e) {
        this.alert = true
      }
    },
    async loadInformationOfCoin(id) {
      try{
        let response = await fetch('https://api.coinpaprika.com/v1/coins/' + id, {method: 'GET'})
        if (response.status !== 200) {
          throw 'error loading'
        }
        let data = await  response.json()
        this.$set(this.informationOfCoins, id, data)
      }catch(e) {
        this.popup.alert = true
      }
    }
  }
}
</script>

<style>
  .coins-list{
    border: 1px solid #aaa;
    overflow: hidden;
    margin: 25px auto 0;
    width: 94%;
  }
  .coins-list__ul {
    padding: 0;
    margin: 0;
  }

  .coins-list__more-button {
    width: 100px;
    background: transparent;
    border: 1px solid #aaa;
  }
</style>
