<template>
  <h2 class="loading">{{'Загрузка' + dots}}</h2>
</template>

<script>
export default {
  name: 'Loading',
  data() {
    return {
      dots: '.',
      timer: null
    }
  },
  mounted() {
    this.timer = setInterval(this.animateDots, 500)
  },
  unmounted() {
    clearInterval(this.timer)
  },
  methods: {
    animateDots() {
      if (this.dots.length > 2){
        this.dots = '.';
      }else {
        this.dots += '.';
      }
    }
  }
};
</script>

<style>
.loading{
  width: 120px;
  text-align: left;
  display: block;
  margin: 50px auto;
}
</style>
